.Sidebar {
    position: absolute;
    top: 1em;
    left: 1em;
    width: 30vw;
    z-index: 150;
    background-color: rgba(0, 0, 0, 0.5);
    border: 1px solid black;
    border-radius: 10px;
    padding: 5px;
    color: white;
    font-size: 1em;
    font-weight: bold;
    text-align: center;
    max-height: 90%;
    overflow: hidden;
    overflow-y : auto;
}

.Sidebar > .Item {
    background-color: rgba(0, 0, 0, 0.5);
    border: 1px solid black;
    border-radius: 8px;
    padding: 3px;
    margin: 1px 0;
}

.Sidebar > .Item:hover {
    border: 1px solid whitesmoke;
    background-color: rgba(40, 40, 40, 0.5);
    cursor: pointer;
}

.Sidebar > .Item > div:first-child {
    font-size: 70%;
    text-align: left;
}

@media (max-width: 1200px) {
    .Sidebar > .Item > div:first-child {
        text-align: center;
    }
    .Sidebar > .Item > div:first-child > span {
        display: none;
    }
}